export default {
	data() {
		return {
			mobileTip:'',
			queryDto: {
				name: '',
				password: ''
			},
			user:[]
		};
	},
	onLoad() {},
	methods: {
		signIn: function(queryDto) {
			if (this.queryDto.name == '' || this.queryDto.password == '') {
				this.mobileTip = '信息不完备'
			} else {
			uni.request({
				url: this.$baseUrl + '/user/signin',
				method: 'POST',
				data: {
					equalsString: queryDto.name,
					password: queryDto.password
				},
				header: {
					'content-type': 'application/json'
				},
				success: res => {
					console.log(res.data.data);
					if (res.data.code === 1) {
						uni.showToast({
							title: '登录成功'
						});
						uni.switchTab({
							url:'/pages/index/index'
						})
						uni.setStorage({
							key: 'user',
							data: res.data.data,
							success: function() {
								console.log('success');
							}
						});
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		}
		}
	
	}
};
